.cardCon {
  //padding: 1.4rem;
  margin: 1rem;
  margin-block: 1.85rem;
   
}

.imgCon {
  position: relative;

  .img1 {
    border-radius: 9px;
    max-width: 176px;
    max-height: 235px;
  }

  .soldOutxt {
    position: absolute;
    top: 8px;
    left: 8px;
    padding-inline: 0.4rem;
    padding-block: 0.2rem;
    background-color: #fff;
    border-radius: 2px;
    text-transform: uppercase;
    font-size: 0.65rem;
    text-align: center;
  }
}
 
.rev {
  display: flex;

  .star {
    height: 1rem;
    align-self: center;
    margin-right: 0.2rem;
  }
  .numrev {
    font-weight: 200;
  }
}

.title{
    font-weight: 300;
    font-size: 12px;
 
}
