@import "breakpoints";
.imgHero{
    display: flex;
    justify-content: center;
    place-items: center;
    flex-direction: column;
    
}
.img{
    max-width: 80%;
    padding-bottom: .3 rem;
    height: auto;

    @include media-breakpoint-up(large){
        max-width: 90%;
    }
}


.h2{
    font-size:clamp(1.75rem, 5vw, 2.1rem);
}

.lh{
    line-height: 17px;
    &.mt{
        margin-top: .5rem;
      }
    & .fwlight{
        font-weight: 400;
      }
}


  
 