@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;300,400;0,500;1,600&display=swap');

*, ::after,::before{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.img-size{
  width: 80px;
  margin-left: .4rem;
}

.shadow{
  box-shadow: 0px 2.98px 7.46px 0px rgb(0, 0, 0 , 0.10);
}

.con{
  margin: 1rem;
 
}

.mt-1{
  margin-top: .8rem;
}

.mt{
  margin-top: .3rem;
}

.fw-light{
  font-weight: 400;
}

.card{
  display: flex;
